.free__course__img{
    position: relative;
    width: 100%;
    height: 100%;
    top:0;
    left:0;
    z-index:9999;
}

.free__btn{
    position: absolute;
    bottom: -20px;
    right: 20px;
    z-index: 999999;
    padding: 5px 15px;
    font-size: 0.9rem;
}

.free__course__img img{
    border-radius: 10px;
}

.free__course___details h6{
    font-weight: 500;
    line-height: 2rem;
}