.choose__img img{
    border-radius: 15px;
}

.choose__content{
    padding-right: 50px;
}

.choose__img{
    position: relative;
    top:0;
    left:0;
    width: 100%;
    height: 100%;
    z-index: 777;
}

.play__icon{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: #fff;
    z-index: 7777;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.play__icon i{
    color: #17bf9e;
    font-size: 2rem;
    padding: 10px;
}

