@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Andada+Pro:wght@400;500;600;700;800&display=swap);
/* =========== base style ========= */

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  section,
  footer {
    padding-top: 70px;
    padding-bottom: 70px;
  }
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    font-size: 2rem;
    line-height: 50px;
    font-family: "Poppins", sans-serif;
    color: #0a2b1e;
    font-weight: 600;
  }
  
  p {
    color: #596b65;
    font-size: 1rem;
    line-height: 35px;
    font-family: "Andada Pro", serif;
    font-weight: 400;
  }
  
  button.btn {
    background: #17bf9e;
    color: #fff;
    padding: 7px 25px;
    border-radius: 50px;
  }
  
  i {
    color: #17bf9e;
  }
  
.header {
    width:100%;
    height: 80px;
    line-height: 80px;
}
 
.nav__list {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    grid-column-gap: 1.3rem;
    -webkit-column-gap: 1.3rem;
            column-gap: 1.3rem;
}

.nav__item a{
    font-weight: 500;
    font-size: 1.1rem;
    color: #0a2b1e;
    text-decoration: none;
    transition: 0.3s;
}

.nav__item a:hover {
    color: #17bf9e;
}

.nav__right p{
    font-weight: 500;
    color: #0a2b1e;
    font-size: 1.1rem;

}

.nav__right p i{
    font-size: 1.3rem;
}

.mobile__menu span i{
    font-size: 1.4rem;
    color: #0a2b1e;
}

.mobile__menu {
    display: none;
}

@media only screen and (max-width: 991px){
    .nav__menu{
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: #0a2b1eb2;
        z-index: 99;
        display: none;
    }

    .nav__list{
        position: absolute;
        background: #fff;
        top:0;
        right: 0;
        width: 250px;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        z-index: 999;
        padding-top: 80px;
    }

    .mobile__menu{
        display: block;
    }

    .nav__right{
        display: none;
    }

    .active__menu{
        display: block;
    }
}
.hero__content h2{
    font-weight: 600;
    line-height: 55px;
}

.hero__content{
    padding-top: 80px;
}

.search input{
    border:none;
    outline: none;
    padding: 7px 10%;
}

.search {
    width: 60%;
    border: 1px solid #17bf9d44;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 50px;
    margin-top: 20px;
}




.counter {
    font-size: 2rem;
    font-weight: 600;
    color: #0a2b1e;
}

.counter__title{
    font-size: 1rem;
    color:#0a2b1e;
    font-weight: 500;
}

.about__content{
    padding-left: 25px;
}
.single__course__item {
    padding: 15px;
}

.course__img img{
    border-radius: 10px;
    margin-bottom: 20px;
}

.enroll a{
    text-decoration: none;
    font-weight: 600;
    font-size: 0.9rem;
    color: #17bf9e;
}

.lesson, .students, .rating {
    font-size: 0.9rem;
    color: #0a2b1e;
    font-weight: 500;
}

.lesson i, .students i, .rating i{
    font-weight: 400;
}

.course__title {
    line-height: 2rem;
}
.choose__img img{
    border-radius: 15px;
}

.choose__content{
    padding-right: 50px;
}

.choose__img{
    position: relative;
    top:0;
    left:0;
    width: 100%;
    height: 100%;
    z-index: 777;
}

.play__icon{
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    background: #fff;
    z-index: 7777;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.play__icon i{
    color: #17bf9e;
    font-size: 2rem;
    padding: 10px;
}


.single__feature h2 i {
    font-size: 2.5rem;
}

.single__feature h6{
    font-weight: 600;

}
.free__course__img{
    position: relative;
    width: 100%;
    height: 100%;
    top:0;
    left:0;
    z-index:9999;
}

.free__btn{
    position: absolute;
    bottom: -20px;
    right: 20px;
    z-index: 999999;
    padding: 5px 15px;
    font-size: 0.9rem;
}

.free__course__img img{
    border-radius: 10px;
}

.free__course___details h6{
    font-weight: 500;
    line-height: 2rem;
}
.testimonial__content h2{
    font-weight: 600;
}

.testimonial__img img{
    margin-right: 25px;
    border-radius: 5px;
}

.testimonial__img {
    margin-right: 25px;
}
.newsletter{
    background: #17bf9e;
    color: #fff;
    padding: 60px;
    border-radius: 15px;
}

.newsletter h2{
    color: #fff;
}

.subscribe input {
    border: none;
    outline: none;
}

.subscribe {
    padding: 7px 10px;
    background: #fff;
    width: 45%;
    margin: auto;
    border-radius: 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;

}


.footer{
    background: rgba(226, 248, 248, 0.664);
}

.follows span a{
    text-decoration: none;

}

.follows span{
    margin-left: 15px;

}

.link__list .link__item {
    background: transparent;
}

.link__list a{
    text-decoration: none;
    color: #0a2b1e;
    font-weight: 500;
}
